import React, { useEffect, useRef } from 'react';
import { TypeHarmonic } from 'interface/ui.interface';
import { $ } from 'components';
import './Harmonic.scss';

export const Harmonic: React.FC<TypeHarmonic> = ({
  submit,
  name,
  children,
}) => {
  const ref = useRef<HTMLDivElement>(null);

  const handlerClick = (e: React.MouseEvent<HTMLElement>) => {
    const target = e.target as HTMLElement;
    const parent = $(target).closest(`.harmonic`);

    if (target.closest('li') as HTMLElement) {
      addClass(target, 'li');
    } else if (parent.length && parent.has('add')) {
      parent.remove('add');
      parent.children('.harmonic__body').style('maxHeight', 0);
    } else if (parent.length) {
      parent.add('add');
      parent
        .children('.harmonic__body')
        .style(
          'maxHeight',
          parent.children('.harmonic__items').elem.clientHeight
        );
    }
  };

  const addClass = (target: HTMLElement, name: string) => {
    if (!$(target).closest(`${name}`).has('add')) {
      $(`${name}`).remove('add');
      $(target.closest(`${name}`) as HTMLElement).toggle('add');
    }
  };

  useEffect(() => {
    if (submit && ref.current) {
      ref.current.classList.add('add');
      $(ref.current)
        .children('.harmonic__body')
        .style(
          'maxHeight',
          $(ref.current).children('.harmonic__items').elem.clientHeight
        );
    }
  }, []);

  return (
    <div ref={ref} onClick={handlerClick} className={`${name}}  harmonic`}>
      <div className='harmonic__head'>
        <p className=''>{name}</p>
        <p className='harmonic__arrow '>{'>'}</p>
      </div>
      <div className='harmonic__body'>
        <ul className='harmonic__items'>{children}</ul>
      </div>
    </div>
  );
};
